import React,{Component} from 'react';
class  NewCreds extends Component {
    state = { 
       
     }
    handleSubmit = (e) =>{
       e.preventDefault();
       const {handleReceivedCreds} = this.props;
       handleReceivedCreds(this.state)
    }
    handleChange = (e) =>{
        e.preventDefault();
        this.setState({
            [e.target.id]:e.target.value
        })
    }
    render() {
        return ( 
            <div>
                <form onSubmit={this.handleSubmit} id='form-container'>
                <h1>New Credential</h1>
                <h5 id='new-credential-warning'></h5>
                    <input type='email' id='email' onChange={this.handleChange} placeholder='Email'/>
                    <input type='password' id='password' onChange={this.handleChange} placeholder='Password'/>
                    <input type='text' id='username' onChange={this.handleChange} placeholder='Username'/>
                    <input type='appName' id='appName' onChange={this.handleChange} placeholder='Application/tool Name'/>
                    <a href='#h1Link'><button type='submit' onSubmit={this.handleSubmit}>Submit</button></a>
                 </form>
            </div>
         );
    }
}
 
export default NewCreds;