import React, { Component } from 'react';
import NewCreds from './components/NewCred';
import Search from './components/search';
import './style.css';

class creds {
  constructor(email, username, password, appName) {
    this.email = email;
    this.username = username;
    this.password = password;
    this.appName = appName;

  }
}

export default class App extends Component {
  state = {
    Credential: [
      new creds('harrishero2003@gmail.com', 'harrishero', 'peli1234', 'codewars'),
      new creds('harrishero2004@gmail.com', 'harrishero1', 'peli12354', 'codewars'),
      new creds('harrishero2003@gmail.com', 'harrishero', 'peli1234', 'others'),

    ]
  }
  savingStatetoStorage = () => {
    localStorage.setItem("restoreDOM", document.getElementById('container').innerHTML);
    localStorage.setItem("restoreSTATE", this.state);
    console.log('data saved');
  }
  restoreStatetoStorage = () => {
    var warning =  document.getElementById('warning');
    if (localStorage.getItem("restoreDOM") === null || localStorage.getItem("restoreSTATE") === null) {
      warning.innerHTML = `*It seems like there isn't any saved data `;
      this.savingStatetoStorage();
      this.restoreStatetoStorage();
      warning.style.color = 'red';
    }
    else {
      document.getElementById('container').innerHTML = localStorage.getItem("restoreDOM");
      this.state = localStorage.getItem('restoreSTATE');
      console.log('data loaded')

      warning.innerHTML = `Data found and loaded successfully `;
      warning.style.color = 'green';
    }
  }

  handleReceivedCreds = (ReceivedData) => {
    var warning =  document.getElementById('new-credential-warning');
    let email, password;
    email = prompt('Enter your Email Address: ');
    password = prompt('Enter your password: ');
    if (email == 'harrishero2003@gmail.com' && password == '1551') {
      alert('Data Received');
      warning.innerHTML = `&#128193 Data inserted successfully &#128193 `;
      warning.style.color = 'green';
      var InsertCred = new creds(ReceivedData.email, ReceivedData.username, ReceivedData.password, ReceivedData.appName);
      console.log(InsertCred)

      this.setState({
        Credential: [...this.state.Credential, InsertCred]
      })
      setTimeout(() => {
        this.savingStatetoStorage();
      }, 1000)
    }
    else{
      warning.innerHTML = '&#128271 Sorry, Admin access denied. &#128271'
      warning.style.color = 'red';
    }
  }


render() {
  return (
    <div id='container'>
      <Search dataState={this.state} />
      <NewCreds handleReceivedCreds={this.handleReceivedCreds} />
      <h1 id='h1Link'>My Credentials</h1>
      <h5 className='red' id='warning'></h5>
      <h5>Data not <span className='red'>found?</span></h5>
      <div id='LoadOrSave'>
        {/* <button onClick={this.savingStatetoStorage}>Save Data</button><br/> */}
        <button onClick={this.restoreStatetoStorage}>Load Data</button>
      </div>

      <div id='cred-container'>
        {this.state.Credential.map((data) => {
          return (
            <div className='cred-container-inner'>
              <h2>Application: {data.appName}</h2><br/>
              <p><b>Email:</b> {data.email}</p>
              <p><b>Username:</b>{data.username}</p>
              <p><b>Password:</b> <span className='private-password'><button onClick={
                (e) => {
                  e.preventDefault();
                  let email = prompt('Enter your Email Address: ');
                  let password = prompt('Enter your password: ');
                  if (email == 'harrishero2003@gmail.com' && password == '1551') {
                    alert(`Username: ${data.username} \nPassword: ${data.password} \nApplication: ${data.appName}`)
                  }
                  else {
                    alert('Wrong Information,\nPassword will not be shown');

                  }
                }
              }>On Password</button></span></p>
            </div>
          )
        })}
      </div>
    </div>
  )
}
}
