import React, { Component } from 'react';

export default class Search extends Component {
    state = {

    }
    handleChange = (e) => {
        //  e.preventDefault();
        let { dataState } = this.props;

        setTimeout(() => {
            var Currentstate = dataState.Credential;
            var newState = [];
            for(var x = 0; x < Currentstate.length; x++){
                if(e.target.value == Currentstate[x].appName){
                    // console.log(Currentstate[x])
                    newState.push(Currentstate[x]);
                }
                this.setState({
                   newState
                })
                console.log(this.state)
                console.log('searched found...');
            }
        })
}
    handleReturn = (appName,username, password) => {
        return (
            (e) => {
                e.preventDefault();

                var askemail = '';
                var askpassword = '';
                askemail = prompt('Enter your Email Address: ');
                askpassword = prompt('Enter your password: ');
                if (askemail == 'harrishero2003@gmail.com' && askpassword == '1551') {
                    alert(`Username: ${username} \nPassword: ${password} \nApplication: ${appName}`)
                }
                else {
                    alert('Wrong Information,\nPassword will not be shown');

                }
            }
        )
    }
    handleSubmit = (e) => {
        e.preventDefault();
        var data = this.state.newState
        console.log(data)
        var display_div = document.getElementById('display');
        if(data == undefined || data.length == 0 || document.getElementById('search').value == ''){
            console.log('not found');
            display_div.innerHTML = '<h2>&#128557 Data does not exist &#128557</h2>'
            display_div.style.color = 'red';
        }
        else{
            display_div.innerHTML = '<h2>&#128513 Data found &#128519</h2><br/>';
            display_div.style.color = 'green';
            console.log('found')
            data.map((filteredData)=>{
                console.log(filteredData.email)
                var newDiv = document.createElement('div');
                newDiv.className = "cred-container-inner";
                newDiv.innerHTML = `
                <h2>Application: ${filteredData.appName}</h2><br/>
                <p><b>Email:</b> ${filteredData.email}</br>
                <b>Username:</b>${filteredData.username}</p>
                <p><b>Password:</b> <span className='private-password'>
                <span className='private-password'>
                <button class='on-password'>on Password</button>
                `;
                display_div.appendChild(newDiv);
                document.getElementById('search').value = '';
            })
            var btns = document.getElementsByClassName('on-password');
                for(var x = 0; x < btns.length; x++){
                    btns[x].addEventListener('click',this.handleReturn(data[x].appName,data[x].username,data[x].password));
                } 
            
    }


        
        
    }
    handleClick = (e) => {
        console.log('hey')
        console.log(e.target.value)
        const clearSpace = e.target.value.length ? (e.target.value = '') : (console.log('the coast is clear sir...'))
        if (e.target.value == '') {
            this.setState({
                newState:undefined
            })
            document.getElementById('display').innerHTML = ''

        }
    }
    render() {
        return (
            <div className='search-container'>
                <h1>Search Credential</h1>
                <form onSubmit={this.handleSubmit}>
                    <input type='text' id='search' placeholder='search...' onChange={this.handleChange} onClick={this.handleClick} />
                    <br /><button type='submit' onClick={this.handleSubmit}>Search</button>
                </form>
                <div id='display'>
                    <p>
                        
                    </p>
                </div>
            </div>
        )
    }
}